import { useState, FormEvent } from "react";
import Head from "next/head";
import Link from "next/link";
import { NextPage } from "next";

type AuthMode = "login" | "signup";

const AuthPage: NextPage = () => {
  const [mode, setMode] = useState<AuthMode>("login");
  const [email, setEmail] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [name, setName] = useState<string>("");
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    try {
      if (mode === "login") {
        console.log("Login attempt with:", email, password);
        // Add your login logic here
        await new Promise((resolve) => setTimeout(resolve, 1000));
      } else {
        console.log("Signup attempt with:", name, email, password);
        // Add your signup logic here
        await new Promise((resolve) => setTimeout(resolve, 1000));
      }

      // On success, redirect to home page
      // router.push('/');
    } catch (err) {
      setError(
        err instanceof Error
          ? err.message
          : mode === "login"
          ? "Login failed"
          : "Signup failed"
      );
    } finally {
      setIsLoading(false);
    }
  };

  const toggleMode = () => {
    setMode((prev) => (prev === "login" ? "signup" : "login"));
    setError(null);
  };

  return (
    <div className="relative flex h-screen w-screen flex-col bg-black md:items-center md:justify-center md:bg-transparent">
      <Head>
        <title>{mode === "login" ? "Sign In" : "Sign Up"} - Netflix</title>
        <meta name="description" content="Netflix Clone Authentication" />
        <link rel="icon" href="/favicon.ico" />
      </Head>

      {/* Background */}
      <div className="absolute -z-10 h-full w-full bg-black opacity-60" />
      <img
        src="https://assets.nflxext.com/ffe/siteui/vlv3/d0982892-13ac-4702-b9fa-87a410c1f2da/519e3d3a-1c8c-4fdb-8f8a-7eabdbe87056/EG-en-20220321-popsignuptwoweeks-perspective_alpha_website_large.jpg"
        alt="Netflix Background"
        className="absolute -z-20 h-full w-full object-cover"
      />

      {/* Netflix Logo */}
      <Link href="/" className="absolute left-4 top-4 md:left-10 md:top-6">
        <div className="cursor-pointer">
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/0/08/Netflix_2015_logo.svg"
            className="h-8 md:h-12"
            alt="Netflix Logo"
            width={150}
            height={40}
          />
        </div>
      </Link>

      {/* Auth Form */}
      <form
        onSubmit={handleSubmit}
        className="relative mt-24 space-y-6 rounded bg-black/75 py-8 px-6 md:mt-0 md:max-w-md md:px-14"
      >
        <h1 className="text-3xl font-semibold">
          {mode === "login" ? "Sign In" : "Sign Up"}
        </h1>

        {error && (
          <div className="p-3 bg-red-600 text-white rounded text-sm">
            {error}
          </div>
        )}

        <div className="space-y-4">
          {mode === "signup" && (
            <label className="inline-block w-full">
              <input
                type="text"
                placeholder="Name"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="input"
                required
              />
            </label>
          )}

          <label className="inline-block w-full">
            <input
              type="email"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="input"
              required
            />
          </label>

          <label className="inline-block w-full">
            <input
              type="password"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="input"
              required
              minLength={4}
            />
          </label>
        </div>

        <button
          type="submit"
          disabled={isLoading}
          className={`w-full rounded bg-[#e50914] py-3 font-semibold ${
            isLoading ? "opacity-60" : ""
          }`}
        >
          {isLoading
            ? mode === "login"
              ? "Signing In..."
              : "Signing Up..."
            : mode === "login"
            ? "Sign In"
            : "Sign Up"}
        </button>

        <div className="flex justify-between items-center text-[#737373] text-sm">
          <div className="flex items-center">
            <input type="checkbox" id="remember" className="mr-1" />
            <label htmlFor="remember">Remember me</label>
          </div>
          <a href="#" className="hover:underline">
            Need help?
          </a>
        </div>

        <div className="text-[#737373]">
          {mode === "login" ? (
            <>
              New to Netflix?{" "}
              <button
                type="button"
                onClick={toggleMode}
                className="text-white hover:underline"
              >
                Sign up now
              </button>
            </>
          ) : (
            <>
              Already have an account?{" "}
              <button
                type="button"
                onClick={toggleMode}
                className="text-white hover:underline"
              >
                Sign in now
              </button>
            </>
          )}
        </div>

        <div className="text-[#737373] text-xs">
          <p>
            This page is protected by Google reCAPTCHA to ensure you&apos;re not a
            bot.{" "}
            <a href="#" className="text-blue-500 hover:underline">
              Learn more.
            </a>
          </p>
        </div>
      </form>
    </div>
  );
};

export default AuthPage;
